function* quips(name) {
    yield " " + name + "!";
    yield "ES6";
    if (name.startsWith("X")) {
        yield " " + name + "  X";
    }
    yield "";
}

var generators = function() {
    console.info('------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------');

    // 

    // functionfunction*
    // returnyieldreturnyieldyieldyield

    // 
    var my_quips = quips('Xiong');
    console.log(my_quips);

    console.log(my_quips.next());
    console.log(my_quips.next());
    console.log(my_quips.next());
    console.log(my_quips.next());
    console.log(my_quips.next());

    // returnJS

    // quips("jorendorff")iter

    // .next()yield

    // iter.next()quips()yield

    // iter.next()donetruevalueundefined

    // yields.next()

    // yield

    // 

    // rangeCfor(;;)

    class RangeIterator {
        constructor(start, stop) {
            this.value = start;
            this.stop = stop;
        }

        [Symbol.iterator]() {
            return this;
        }

        next() {
            var value = this.value;
            if (value < this.stop) {
                this.value++;
                return {
                    done: false,
                    value: value
                }
            } else {
                return {
                    done: true,
                    value: undefined
                }
            }
        }
    }

    function range(start, stop) {
        return new RangeIterator(start, stop);
    }


    for (var value of range(0, 3)) {
        console.log("Ding!at floor #" + value);
    }

    //     JavaSwiftbugfor (;;)

    // 

    // JS

    function* range2(start, stop) {
        for (var i = start; i < stop; i++) {
            yield i;
        }
    }

    for (var value of range2(0, 3)) {
        console.log("Ding!at floor #" + value);
    }


    //     4RangeIterator23.next()[Symbol.iterator]()

    // RangeIterator

    // 


    // l yield[Symbol.iterator]

    // l 

    function splitIntoRows(icons, rowLength) {
        var rows = [];
        for (var i = 0; i < icons.length; i += rowLength) {
            rows.push(icons.slice(i, i + rowLength));
        }
        return rows;
    }

    // 

    function* splitIntoRows2(icons, rowLength) {
        for (var i = 0; i < icons.length; i += rowLength) {
            yield icons.slice(i, i + rowLength);
        }
    }

    // 

    // 

    // for (var data of myNewGenerator(args))

    // ES6

    // Array.prototype.filterDOM NodeLists

    function* filter(test, iterable) {
        for (var item of iterable) {
            if (test(item)) {
                yield item;
            }
        }
    }

    // ES6

    // Q.async()promises

    // 
    function makeNoise() {
        shake();
        rattle();
        roll();
    }

    // 
    // Promise
    // resolved
    function makeNoise_async() {
        return Q.async(function*() {
            yield shake_async();
            yield rattle_async();
            yield roll_async();
        });
    }

    // yield

    // Q.asynciftry/catch

    // 
    // [A Study on Solving Callbacks with JavaScript Generators](http://jlongster.com/A-Study-on-Solving-Callbacks-with-JavaScript-Generators)

    function* foo(x) {
        yield x + 1;

        var y = yield null;

        return x + y;
    }

    var gen = foo(5);
    console.log(gen.next());
    console.log(gen.next());
    // console.log(gen.send(8));

}

export default generators;



