var proxiesAndReflect = function() {
    var obj = new Proxy({}, {
        get: function(target, key, receiver) {
            console.log(`getting ${key}!`);

            return Reflect.get(target, key, receiver);
        },
        set: function(target, key, value, receiver) {
            console.log(`setting ${key}!`);

            return Reflect.set(target, key, value, receiver);
        }
    });

    obj.count = 1;
    ++obj.count;

    // setting countgetting count.

    //     ECMAScriptJavaScriptAPI

    // DOM

    // getset
    // JS
    // 

    // JS

    // ECMAScript14

    // -------------------------------

    // ES65614[[ ]]JS

    // obj.[[Get]](key, receiver)  

    // JSobj.propobj[key]

    // objreceiverobjreceiver

    // obj.[[Set]](key, value, receiver)  

    // JSobj.prop = valueobj[key] = value

    // obj.prop += 2[[Get]][[Set]]++--

    // obj.[HasProperty]  

    // JSkey in obj

    // obj.[Enumerate]  

    // JSfor (key in obj) 

    // for-in

    // obj.[GetPrototypeOf]  

    // JSobj.[__proto__]Object.getPrototypeOf(obj)

    // functionObj.[[Call]](thisValue, arguments)  

    // JSfunctionObj()x.method()

    // 

    // constructorObj.[[Construct]](arguments, newTarget)  

    // JSnew Date(2890, 6, 2)

    // 

    // newTarget

    // ----------

    // obj.propObject.keys()

    // ES6Proxytargethandler

    var target = {},
        handler = {};

    var proxy = new Proxy(target, handler);

    // 

    // proxy.[[Enumerate]]()target.[[Enumerate]]()

    // proxy.[[Set]]()

    proxy.color = 'pink';

    // proxy.[[Set]]()target.[[Set]]()

    console.log(target.color);

    // 

    // proxy !== targetDOMdocument.body.appendChild(proxy)TypeError

    // 

    // handler.set()

    handler.set = function(target, key, value, receiver) {
        console.info('');
        return Reflect.set(target, key, value, receiver);
    }

    proxy.name = '12';

    // 

    // Tree()

    // var tree = Tree();

    //     > var tree = Tree();
    // > tree
    //     { }
    // > tree.branch1.branch2.twig = "green";
    // > tree
    //     { branch1: { branch2: { twig: "green" } } }
    // > tree.branch1.branch3.twig = "yellow";
    //     { branch1: { branch2: { twig: "green" },
    //                  branch3: { twig: "yellow" }}}

    // branch1branch2branch3

    // tree.[[Get]]()
    function Tree() {
        return new Proxy({}, treeHandler);
    }

    var treeHandler = {
        get: function(target, key, receiver) {
            if (!(key in target)) {
                target[key] = Tree();
            }
            return Reflect.get(target, key, receiver);
        }
    }

    var tree = Tree();

    console.log(tree);

    tree.branch1.branch2.twig = 'green';

    console.log(tree);

    // Reflect.get()

    // --------------

    // readOnlyView(object)

    // > var newMath = readOnlyView(Math);
    // > newMath.min(54, 40);
    //     40
    // > newMath.max = Math.min;
    //     Error: can't modify read-only view
    // > delete newMath.sin;
    //     Error: can't modify read-only view

    function NOPE(target, key, receiver) {
        console.info("Can't modify read-only view");
        return Reflect.get(target, key, receiver);
    }

    var handler2 = {
        set: NOPE,
        defineProperty: NOPE,
        deleteProperty: NOPE,
        preventExtensions: NOPE,
        setPrototypeOf: NOPE,
        get: function(target, key, receiver) {
            // 
            var result = Reflect.get(target, key, receiver);

            // 
            if (Object(result) === result) {
                // result
                return readOnlyView(result);
            }

            // result
            return result;
        }
    }

    function readOnlyView(target) {
        return new Proxy(target, handler2);
    }

    var newMath = readOnlyView(Math);

    console.log(newMath.min(54, 40));
    newMath.min = newMath.max;

    //     

    // 

    // [[Get]]xx.prop

    // handler.get()

    // getPrototypeOfgetOwnPropertyDescriptor

    // gettergetterthis

    // 

};
export default proxiesAndReflect;



