function destructuring() {
    let someArray = [1, 2, 3];

    let [first, second, third] = someArray;

    console.log('destructuring : ', first, second, third);

    let someArray2 = [1, [
        [2], 3
    ]];

    var [foo, [
        [bar], baz
    ]] = someArray2;

    console.log('destructuring2 : ', foo, bar, baz);

    let [, , third2] = someArray;

    console.log(third2);

    let [head, ...tail] = [1, 2, 3, 4, 5];

    console.log(head, tail);

    // function* fibs()

    function* fibs() {
        var a = 0;
        var b = 1;

        while (true) {
            yield a;
            [a, b] = [b, a + b];
            console.log(a, b);
        }
    }

    var [fibs1, fibs2, fibs3, fibs4, fibs5] = fibs();

    console.log(fibs1, fibs2, fibs3, fibs4, fibs5);

    let robotA = {
        name: 'Bender'
    };
    let robotB = {
        name: 'Flexo'
    };

    let {
        name: nameA
    } = robotA;
    let {
        name: nameB
    } = robotB;

    console.log(nameA, nameB);

    let complicatedObj = {
        arrayProp: [
            "Zapp", {
                com2: 'Brannigan'
            }
        ]
    };

    let {
        arrayProp: [com1, {
            com2
        }]
    } = complicatedObj;

    console.log(com1, com2);

    var [missing = true] = [];
    console.log(missing);

    var {
        message: msg = "Something went wrong"
    } = {};
    console.log(msg);

    var {
        x = 3
    } = {};
    console.log(x);

    //  APIAPI 
    function removeBreakPoint({
        url,
        line,
        column
    }) {

    }

    // jQueryajax

    // var foo = config.foo || theDefaultFoo;
    let JQuery = {};
    JQuery.ajax = function(url, {
        async = true,
        beforeSend = noop,
        cache = true,
        complete = noop,
        crossDomain = false,
        global = true
    }) {

    }

    var map = new Map();
    map.set(window, 'the global');
    map.set(document, 'the document');

    for (var [key, value] of map) {
        console.log(key + ' is ' + value);
    }

    for (var [key] of map) {

    }

    for (var [, key] of map) {

    }

    function returnMultipleValues() {
        return [1, 2];
    }

    var [mul1, mul2] = returnMultipleValues();

    console.log(mul1, mul2);

    function returnMultipleValues2() {
        return {
            foo: 1,
            bar: 2
        };
    }

    var {
        foo,
        bar
    } = returnMultipleValues2();

    console.log(foo, bar)

}
export default destructuring;



